@use './menu.scss';
@use './footer.scss';

html, body, div, p, img, h1, h2, h3, h4, h5, h6{
  box-sizing: border-box;
}

body{
    color:lightblue;
    margin: 0;
    padding:0;
    font-size:16px;
    font-family: sans-serif;
}

h1{
  font-size: 2em;
}

.container{
  width:980px;
  max-width:100%;
  margin: 0 auto;
  padding: 0.5em;
}

.main-image{
  background-image: url('../images/thumbnail.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
  width: 100%;
  height: 600px;
  border-radius: 2em;
  margin:1em 0;
}

$icon-size: 64px;
.icon{
  width: $icon-size;
  height: $icon-size;
  border-radius: calc($icon-size/2);
  padding: 4px;
  position: absolute;
  top:1em;
  left:1em;
}

$text-color: orange;
.content {
  & .title {
    font-size: 24px;
    color: $text-color;
  }
  & .title-lg {
    @extend .title;
    font-size: 32px;
  }
}